<template>
  <div>
    <div class="header">
      <span v-if="this.open" @click="mounted($event)"> &#x25B2 Collapse</span>
      <span v-else="!this.open" @click="mounted($event)"> &#x25BC Expand</span>
    </div>
    <!-- injectible, toggable content - e.g when calling elsewhere -->
    <!-- use as follows: <CollapsibleSection><div>My Content</div></CollapsibleSection> -->
    <slot v-if="this.open">
    </slot>
  </div>
</template>

<script>
  export default {
  	name: 'CollapsibleSection',
    props: ['open','afterMount'],
    method: {
      mounted: function($event) {
        console.log('child1');
        event.$emit('afterMount');
        console.log('child2');
      },
    },
  };
</script>

<style scoped>

  .header {
  	background-color: #bbb;
  	padding: 3px;
  	cursor: pointer;
  }
</style>